import React, { useEffect } from 'react'
import Lottie from 'lottie-react'
import happycust from "./animations/Happycust.json"
import globe from "./animations/Globe.json"
import jaihind from "./animations/jaihind.json"
import project from "./animations/project.json"
import { useState } from 'react'
export default function Infopane() {

  // const [ccc, setccc] = useState(0)
var ccc=0
  window.addEventListener("scroll",reveal)
  function reveal(){
    var reveals=document.querySelectorAll(".infopanecnt")
    var windowheight=window.innerHeight;
    var revealtop=reveals[0].getBoundingClientRect().top
    var revealpoint = 250;
    if (revealtop<windowheight-revealpoint && ccc==0){
      reveals[0].classList.add("reavealactive")
      startcounting();
      ccc=1
      
    }
  }
  function startcounting(){
    
    console.log(ccc)
    let dataval=document.querySelectorAll(".dataval")
    let interval=1500
    dataval.forEach((e)=>{
      let startval=0
      let endval=parseInt(e.getAttribute("data-val"))
      let duration=Math.floor(interval/endval)
      let counter= setInterval(function(){
        startval +=1
        e.textContent=startval
        if(startval==100){
          e.textContent=startval+"%"
        }
        if(startval==200){
          e.textContent=startval+"+"
        }
        if(startval==12){
          e.textContent=startval+"+"
        }
        if(startval==400){
          e.textContent=startval+"+"
        }
        
        if (startval==endval){
          clearInterval(counter)
        }
      },duration)
      
      
    })
    
  }
  
  
   
 
    
  return (
   <>
   
        
      
   <div className='infopanecnt fontclass'>
   
   <div className='infopanechildcnt' >
    <Lottie id='globe' animationData={globe}></Lottie>
     <h1 className='dataval' data-val="12"></h1> 
     <h3>Countries Served</h3>
   </div>
   <div className='infopanechildcnt'>
    <Lottie style={{width:"50%", textAlign:"center",margin:"auto"}} animationData={happycust}></Lottie>
     <h1 className='dataval' data-val="200"></h1>
     
     <h3>Happy Customers</h3> 
   </div>
   <div className='infopanechildcnt'>
    <Lottie  id="happycust" animationData={jaihind}></Lottie>
     <h1 className='dataval' data-val="100+%"  ></h1>
      <h3>Make In India</h3>
     
   </div>
   <div className='infopanechildcnt'>
    <Lottie animationData={project}></Lottie>
     <h1 className='dataval' data-val="400">4</h1>
     <h3>Projects Delivered</h3> 
   </div>
   
   
   </div>
   
   </>
  )
}
